.file-card {
  padding: 1em;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 1px dashed #c5daf3;
  background-color: transparent;
  width: 50%;
  height: 650px;
  margin-left: 25%;
  color: #fff;
  margin-top: 2em;
  font-weight: 600;
  .file-inputs {
    position: relative;

    .inputs:hover {
      color: #fff;
    }

    input {
      position: relative;
      text-align: right;
      opacity: 1;
      z-index: 2;
      cursor: pointer;
    }

    button {
      position: absolute;
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
      z-index: 1;
      display: flex;
      justify-content: center;
      align-items: center;

      color: #fff;
      background-color: transparent;
      font-size: 1.1rem;
      cursor: pointer;
      border-radius: 4px;
      border: none;
      outline: none;
      // padding: 1em;
      transition: background-color 0.4s;
      // text-shadow: red 0 -2px;
      text-shadow: 1px 1px 2px rgb(46, 44, 44), 0 0 1em rgb(1, 1, 10),
        0 0 0.2em rgb(13, 13, 14);
      color: black;
      border: 0.5px dashed #cbd5e0;

      // i {
      //   width: 1.5em;
      //   height: 1.5em;
      //   padding: 0.4em;
      //   background-color: #fff;
      //   color: #f55e30;
      //   border-radius: 50%;
      //   display: flex;
      //   justify-content: center;
      //   align-items: center;
      //   margin-right: 0.8em;
      //   font-size: 0.8em;
      // }
    }

    &:hover {
      button {
        background-color: #fff;
      }
    }
  }
}

.main {
  font-weight: 700;
  font-size: large;
  margin-bottom: 0.4rem;
  margin-top: 1.5rem;
  transition: background-color 0.4s;
  text-shadow: red 0 -1px;
}

.info {
  font-size: 0.8rem;
}

.btn-wrapper .btn-default {
  font-weight: 700;
  width: 80%;
  cursor: pointer;
  height: 50px;
  border: 1px dashed #cbd5e0;
  background: transparent;
  transition: background-color 0.4s;
  // text-shadow: red 0 -2px;
  text-shadow: 1px 1px 2px rgb(46, 44, 44), 0 0 1em rgb(1, 1, 10),
    0 0 0.2em rgb(13, 13, 14);
  color: #fff;
}

.btn-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 530px;
  margin-bottom: 1px;
  width: 320px;
  margin-right: 530px;
}

.btn-wrapper .btn-default:hover {
  background-color: #fff;
  color: black;
}

.main-selector {
  position: relative;
  width: 10% auto;
  width: 320px;
}

.main-selector .gutterBtn {
  text-shadow: 1px 1px 2px rgb(46, 44, 44), 0 0 1em rgb(1, 1, 10),
    0 0 0.2em rgb(13, 13, 14);
  color: #fff;
}

.gutterBtn .value-info {
  display: flex;
  justify-content: center;
  align-items: center;
  justify-content: space-between;
}

.gutterBtn {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.seperat-div {
  display: flex;
  justify-content: end;
  align-items: center;
  margin-right: 50px;
}

.seperat-div .btn-info {
  width: 200px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(17, 16, 17);
  color: #fff;
  transition: transform 0.8s; /* Animation */
  flex-direction: column;
  font-size: 15px;
  cursor: pointer;
  border-radius: 1em;
  font-weight: 1000;
  box-shadow: 0px 10px 30px rgba(157, 240, 157, 0.5);
}

.seperat-div .btn-info:hover {
  background-color: #fff;
  color: rgb(16, 17, 17);
  text-shadow: 1px 1px 2px rgb(46, 44, 44), 0 0 1em rgb(1, 1, 10),
    0 0 0.2em rgb(13, 13, 14);
}

.main-selector .typo-info {
  display: flex;
  justify-content: space-around;
  font-weight: 600;
}

.upload-box {
  font-size: 15px;
  background: black;
  border-radius: 50px;
  box-shadow: 5px 5px 10px rgb(145, 132, 132);
  width: 400px;
  outline: none;
  height: 39px;
  font-family: Arial, Helvetica, sans-serif;
  margin-top: 2px;
}

::-webkit-file-upload-button {
  color: rgb(250, 165, 7);
  background: #206a5d;
  border: none;
  padding: 10px;
  border-radius: 50px;
  box-shadow: 1px 0 1px 1px #6b4559;
  outline: none;
}

::-webkit-file-upload-button:hover {
  background: #fff;
}
